<template>
    <div class="song-list">
        <div class="item" @click="fileLink(song.song_id)">
        <span class="img">
            <img :src="song.pic_small ? song.pic_small : song.pic_big" alt="" width="52" height="52">
        </span>
        <div class="item-text .border-1px">
            <h3 class="item-title">{{song.title}}</h3>
            <p>专辑：{{song.album_title}}</p>
        </div>
        </div>
    </div>
</template>
<script>
import Utils from '@/common/js/utils.js'
export default {
    props: {
        song: {
            type: Object,
            default() {
                return {}
            }
        }
    },
    methods: {
        fileLink(id) {
            Utils.getSong(id)
        }
    }
}
</script>
<style lang="scss" scoped>
@import '../../common/scss/mixin';
@import '../../common/scss/var.scss';
.item{
    margin:5px 0;
    @extend %padding;
    display:flex;
    height: 57px;
    .img{
        img{
            vertical-align: middle;
        }
    }
    .item-text{
        flex:1;
        margin-left: 10px;
        /*border-bottom: 1px solid $day-border;*/
        @include border-1px($day-border);
        .item-title{
            font-weight: normal;
            color:#222;
            margin-top: 5px;
        }
        p{
            color:#717170;
            font-size: 12px;
        }
    }
}
</style>



